.card {
  box-sizing: border-box;
  width: $card-size;
  padding: 32px 16px 16px;
  margin-right: $card-margin-right;
  margin-bottom: $card-margin-bottom;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;

  &__image {
    display: block;
    width: 160px;
    height: 134px;
    margin: 0 3px 40px;
  }

  &__headline {
    margin-bottom: 4px;
    line-height: 18px;
    @extend %text-medium;
    color: $main-accent-color;
  }

  &__code {
    margin-bottom: 16px;
    @extend %text-small;
    line-height: 14px;
  }

  &__revies {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 24px;
  }

  &__count-review {
    @extend %text-small;
    line-height: 14px;
    color: $main-accent-color;
  }

  &__cost-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
  }

  &__cost {
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
    color: $secondary;
  }

  &__how-cost {
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    color: $main-accent-color;
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    box-sizing: border-box;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    color: #fff;
    background: $blue-accent;
    border-radius: 5px;
    text-decoration: none;

    &:hover {
      background: #fff;
      border: 1px solid $blue-accent;
      color: $blue-accent;
    }

  }

  &:nth-child(4n) {
    margin-right: 0;
  }

  &:nth-last-child(-n+4) {
    margin-bottom: 0;
  }
}
